import React from "react";

const ToDoLists = ({key, data, editHandler, deleteHandler}) => {
  console.log(data);
  const { _id, title, description } = data;

  return (
    <li key={_id}>
      <div className="list">
        <h3>{title}</h3>
        <p>{description}</p>
      </div>

      <div className="btns">
        <button className="add-btn" name={_id} onClick={editHandler}>
          Edit
        </button>
        <button className="delete-btn" name={_id} onClick={deleteHandler}>
          Delete
        </button>
      </div>
    </li>
  );
};

export default ToDoLists;
